.Advice-machine {
  /* Sizing */
  margin-top: 100px;
  width: 100%;
  height: 100%;

  /* Flexbox stuff */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

a {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 210px;
  height: 50px;
  background: red;
  border-radius: 999px;
  margin-top: 35px;
  box-shadow: 5px 5px 5px #000;
  font-family: "Baloo Bhai", sans-serif;
  font-size: 24px; }
  a:hover {
    -webkit-animation: burn 1000ms ease-out forwards;
            animation: burn 1000ms ease-out forwards; }
    a:hover::before {
      content: "";
      position: absolute;
      left: 40px;
      width: 100px;
      height: 40px;
      background: #ffe66e;
      border-radius: 100%;
      -webkit-animation: flare 1000ms ease-out forwards;
              animation: flare 1000ms ease-out forwards; }
    a:hover::after {
      content: "";
      position: absolute;
      right: 40px;
      width: 100px;
      height: 40px;
      background: #ffe66e;
      border-radius: 100%;
      -webkit-animation: flare 1000ms ease-out forwards;
              animation: flare 1000ms ease-out forwards; }

@-webkit-keyframes flare {
  100% {
    -webkit-transform: translateY(-20px) scale(1.5);
            transform: translateY(-20px) scale(1.5);
    -webkit-filter: blur(10px);
            filter: blur(10px);
    opacity: 0; } }

@keyframes flare {
  100% {
    -webkit-transform: translateY(-20px) scale(1.5);
            transform: translateY(-20px) scale(1.5);
    -webkit-filter: blur(10px);
            filter: blur(10px);
    opacity: 0; } }

@-webkit-keyframes burn {
  0% {
    color: #ff826e;
    background: #ffe66e;
    box-shadow: 0 0 5px 0 #c8000a, 0 0 5px 0 rgba(230, 30, 10, 0.8), 0 0 5px 0 rgba(230, 230, 10, 0.6); }
  100% {
    color: black;
    background: white;
    box-shadow: 0 -35px 40px 30px rgba(255, 130, 10, 0), 0 -30px 30px 10px rgba(230, 30, 10, 0), 0 -20px 10px 0 rgba(255, 255, 10, 0); } }

@keyframes burn {
  0% {
    color: #ff826e;
    background: #ffe66e;
    box-shadow: 0 0 5px 0 #c8000a, 0 0 5px 0 rgba(230, 30, 10, 0.8), 0 0 5px 0 rgba(230, 230, 10, 0.6); }
  100% {
    color: black;
    background: white;
    box-shadow: 0 -35px 40px 30px rgba(255, 130, 10, 0), 0 -30px 30px 10px rgba(230, 30, 10, 0), 0 -20px 10px 0 rgba(255, 255, 10, 0); } }

* {
  box-sizing: border-box;
  line-height: 1.5;
  font-family: "Baloo Bhai", sans-serif; }

img {
  max-width: 100%; }

.container {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 6px;
  background: black; }

.card {
  position: relative;
  background: black;
  width: 400px;
  height: 60vh;
  border-radius: 6px;
  padding: 2rem;
  color: #fff;
  box-shadow: 0 0.25rem 0.25rem rgba(0, 0, 0, 0.2), 0 0 1rem rgba(0, 0, 0, 0.2);
  overflow: hidden; }
  .card__image-container {
    margin: -2rem -2rem 1rem -2rem; }
  .card__line {
    opacity: 0;
    -webkit-animation: LineFadeIn 0.8s 0.8s forwards ease-in;
            animation: LineFadeIn 0.8s 0.8s forwards ease-in; }
  .card__image {
    opacity: 0;
    -webkit-animation: ImageFadeIn 0.8s 1.4s forwards;
            animation: ImageFadeIn 0.8s 1.4s forwards; }
  .card__title {
    color: white;
    margin-top: 0;
    font-weight: 800;
    letter-spacing: 0.01em; }
  .card__content {
    margin-top: 1rem;
    opacity: 0;
    -webkit-animation: ContentFadeIn 0.8s 1.6s forwards;
            animation: ContentFadeIn 0.8s 1.6s forwards; }
  .card__svg {
    position: absolute;
    left: 0;
    top: 115px; }
  .card__text {
    font-size: 25px; }

@-webkit-keyframes LineFadeIn {
  0% {
    opacity: 0;
    d: path("M 0 300 Q 0 300 0 300 Q 0 300 0 300 C 0 300 0 300 0 300 Q 0 300 0 300 ");
    stroke: #fff; }
  50% {
    opacity: 1;
    d: path("M 0 300 Q 50 300 100 300 Q 250 300 350 300 C 350 300 500 300 650 300 Q 750 300 800 300");
    stroke: #888bff; }
  100% {
    opacity: 1;
    d: path("M -2 100 Q 50 200 100 250 Q 250 400 350 300 C 400 250 550 150 650 300 Q 750 450 802 400");
    stroke: #545581; } }

@keyframes LineFadeIn {
  0% {
    opacity: 0;
    d: path("M 0 300 Q 0 300 0 300 Q 0 300 0 300 C 0 300 0 300 0 300 Q 0 300 0 300 ");
    stroke: #fff; }
  50% {
    opacity: 1;
    d: path("M 0 300 Q 50 300 100 300 Q 250 300 350 300 C 350 300 500 300 650 300 Q 750 300 800 300");
    stroke: #888bff; }
  100% {
    opacity: 1;
    d: path("M -2 100 Q 50 200 100 250 Q 250 400 350 300 C 400 250 550 150 650 300 Q 750 450 802 400");
    stroke: #545581; } }

@-webkit-keyframes ContentFadeIn {
  0% {
    -webkit-transform: translateY(-1rem);
            transform: translateY(-1rem);
    opacity: 0; }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1; } }

@keyframes ContentFadeIn {
  0% {
    -webkit-transform: translateY(-1rem);
            transform: translateY(-1rem);
    opacity: 0; }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1; } }

@-webkit-keyframes ImageFadeIn {
  0% {
    -webkit-transform: translate(-0.5rem, -0.5rem) scale(1.05);
            transform: translate(-0.5rem, -0.5rem) scale(1.05);
    opacity: 0;
    -webkit-filter: blur(2px);
            filter: blur(2px); }
  50% {
    opacity: 1;
    -webkit-filter: blur(2px);
            filter: blur(2px); }
  100% {
    -webkit-transform: translateY(0) scale(1);
            transform: translateY(0) scale(1);
    opacity: 1;
    -webkit-filter: blur(0);
            filter: blur(0); } }

@keyframes ImageFadeIn {
  0% {
    -webkit-transform: translate(-0.5rem, -0.5rem) scale(1.05);
            transform: translate(-0.5rem, -0.5rem) scale(1.05);
    opacity: 0;
    -webkit-filter: blur(2px);
            filter: blur(2px); }
  50% {
    opacity: 1;
    -webkit-filter: blur(2px);
            filter: blur(2px); }
  100% {
    -webkit-transform: translateY(0) scale(1);
            transform: translateY(0) scale(1);
    opacity: 1;
    -webkit-filter: blur(0);
            filter: blur(0); } }

